'use client';

import { useState, useEffect } from 'react';
import { useAuth } from '@/contexts/AuthContext';
import { 
  ArrowUpIcon, 
  ArrowDownIcon, 
  ChartBarIcon,
  UserIcon, 
  DocumentTextIcon, 
  CurrencyDollarIcon,
  CheckIcon
} from '@heroicons/react/24/outline';

// مكتبة Chart.js مع React
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  BarElement,
  Title,
  Tooltip,
  Legend,
  ArcElement,
  Filler
} from 'chart.js';
import { Line, Bar, Doughnut } from 'react-chartjs-2';

// تسجيل مكونات Chart.js المطلوبة
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  BarElement,
  Title,
  Tooltip,
  Legend,
  ArcElement,
  Filler
);

export default function StatisticsPage() {
  const { user } = useAuth();
  const [loading, setLoading] = useState(true);
  const [period, setPeriod] = useState<'week' | 'month' | 'year'>('month');
  const [stats, setStats] = useState({
    totalEarnings: 0,
    ordersCompleted: 0,
    returningClients: 0,
    avgRating: 0,
    responseRate: 0,
    completionRate: 0,
    currentMonthEarnings: 0,
    lastMonthEarnings: 0,
    earningsPercentChange: 0,
    earningsData: [],
    ordersData: [],
    categoryData: [],
    clientsData: [],
    ratingDistribution: [],
    responseTimeDistribution: []
  });

  useEffect(() => {
    // محاكاة جلب البيانات من API
    const fetchStatistics = async () => {
      setLoading(true);
      
      // بيانات تجريبية للإحصائيات
      let mockData;
      
      // أيام أو أشهر أو سنوات حسب الفترة المحددة
      let labels: string[] = [];
      let earningsValues: number[] = [];
      let ordersValues: number[] = [];
      
      if (period === 'week') {
        labels = ['الأحد', 'الإثنين', 'الثلاثاء', 'الأربعاء', 'الخميس', 'الجمعة', 'السبت'];
        earningsValues = [150, 200, 180, 350, 280, 320, 400];
        ordersValues = [2, 3, 2, 5, 4, 3, 5];
      } else if (period === 'month') {
        labels = Array.from({length: 30}, (_, i) => `${i+1}`);
        earningsValues = Array.from({length: 30}, () => Math.floor(Math.random() * 200) + 100);
        ordersValues = Array.from({length: 30}, () => Math.floor(Math.random() * 3) + 1);
      } else if (period === 'year') {
        labels = ['يناير', 'فبراير', 'مارس', 'أبريل', 'مايو', 'يونيو', 'يوليو', 'أغسطس', 'سبتمبر', 'أكتوبر', 'نوفمبر', 'ديسمبر'];
        earningsValues = [1200, 1500, 1800, 2200, 2500, 3000, 3200, 3500, 3800, 4000, 4200, 4500];
        ordersValues = [15, 18, 20, 25, 28, 30, 32, 35, 38, 40, 42, 45];
      }

      // الإحصائيات الرئيسية
      mockData = {
        totalEarnings: 28500,
        ordersCompleted: 124,
        returningClients: 37,
        avgRating: 4.8,
        responseRate: 98,
        completionRate: 96,
        currentMonthEarnings: 4500,
        lastMonthEarnings: 4000,
        earningsPercentChange: 12.5,
        earningsData: {
          labels,
          datasets: [
            {
              label: 'الأرباح',
              data: earningsValues,
              borderColor: 'rgb(79, 70, 229)',
              backgroundColor: 'rgba(79, 70, 229, 0.1)',
              tension: 0.4,
              fill: true
            }
          ]
        },
        ordersData: {
          labels,
          datasets: [
            {
              label: 'الطلبات',
              data: ordersValues,
              backgroundColor: 'rgb(99, 102, 241)',
              borderRadius: 5
            }
          ]
        },
        categoryData: {
          labels: ['توليد صور', 'كتابة محتوى', 'برمجة', 'تدريب نماذج', 'تحليل بيانات'],
          datasets: [
            {
              label: 'الطلبات حسب الفئة',
              data: [45, 32, 18, 15, 14],
              backgroundColor: [
                'rgb(251, 113, 133)',
                'rgb(99, 102, 241)',
                'rgb(14, 165, 233)',
                'rgb(249, 115, 22)',
                'rgb(34, 197, 94)'
              ],
              borderWidth: 0
            }
          ]
        },
        ratingDistribution: {
          labels: ['5 نجوم', '4 نجوم', '3 نجوم', '2 نجوم', '1 نجمة'],
          datasets: [
            {
              label: 'توزيع التقييمات',
              data: [70, 25, 3, 1, 1],
              backgroundColor: [
                'rgb(34, 197, 94)',
                'rgb(59, 130, 246)',
                'rgb(250, 204, 21)',
                'rgb(249, 115, 22)',
                'rgb(239, 68, 68)'
              ],
              borderWidth: 0
            }
          ]
        }
      };
      
      setTimeout(() => {
        setStats(mockData);
        setLoading(false);
      }, 1000);
    };
    
    fetchStatistics();
  }, [period]);

  const options = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        position: 'top' as const,
      },
      tooltip: {
        rtl: true,
        textDirection: 'rtl'
      }
    },
    scales: {
      y: {
        beginAtZero: true
      }
    }
  };

  const doughnutOptions = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        position: 'top' as const,
      },
      tooltip: {
        rtl: true,
        textDirection: 'rtl'
      }
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="w-12 h-12 rounded-full border-4 border-indigo-300 border-t-indigo-600 animate-spin"></div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div className="flex flex-col sm:flex-row justify-between items-center mb-4">
        <h1 className="text-2xl font-bold text-gray-900">لوحة الإحصائيات</h1>
        
        <div className="inline-flex rounded-md shadow-sm mt-3 sm:mt-0">
          <button
            type="button"
            onClick={() => setPeriod('week')}
            className={`px-4 py-2 text-sm font-medium rounded-r-md border ${
              period === 'week'
                ? 'bg-indigo-600 text-white border-indigo-600'
                : 'bg-white text-gray-700 border-gray-300 hover:bg-gray-50'
            }`}
          >
            أسبوع
          </button>
          <button
            type="button"
            onClick={() => setPeriod('month')}
            className={`px-4 py-2 text-sm font-medium border-t border-b ${
              period === 'month'
                ? 'bg-indigo-600 text-white border-indigo-600'
                : 'bg-white text-gray-700 border-gray-300 hover:bg-gray-50'
            }`}
          >
            شهر
          </button>
          <button
            type="button"
            onClick={() => setPeriod('year')}
            className={`px-4 py-2 text-sm font-medium rounded-l-md border ${
              period === 'year'
                ? 'bg-indigo-600 text-white border-indigo-600'
                : 'bg-white text-gray-700 border-gray-300 hover:bg-gray-50'
            }`}
          >
            سنة
          </button>
        </div>
      </div>
      
      {/* بطاقات الإحصائيات */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
        <div className="bg-white rounded-xl border border-gray-200 shadow-sm overflow-hidden">
          <div className="p-5">
            <div className="flex items-center">
              <div className="flex-shrink-0 bg-indigo-100 rounded-md p-3">
                <CurrencyDollarIcon className="h-6 w-6 text-indigo-700" />
              </div>
              <div className="mr-4">
                <p className="text-sm font-medium text-gray-500">إجمالي الأرباح</p>
                <p className="text-2xl font-bold text-gray-900">{stats.totalEarnings} ر.س</p>
              </div>
            </div>
            <div className="mt-4 flex items-center">
              <div className={`inline-flex items-center px-2 py-0.5 rounded-full text-xs font-medium ${stats.earningsPercentChange >= 0 ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'}`}>
                {stats.earningsPercentChange >= 0 ? (
                  <ArrowUpIcon className="h-3 w-3 ml-1" />
                ) : (
                  <ArrowDownIcon className="h-3 w-3 ml-1" />
                )}
                {Math.abs(stats.earningsPercentChange)}%
              </div>
              <span className="text-gray-500 text-xs mr-2">مقارنة بالشهر الماضي</span>
            </div>
          </div>
          <div className="bg-gray-50 py-3 px-5 border-t border-gray-200">
            <div className="text-sm text-gray-500">
              أرباح الشهر الحالي: <span className="font-semibold text-gray-900">{stats.currentMonthEarnings} ر.س</span>
            </div>
          </div>
        </div>
        
        <div className="bg-white rounded-xl border border-gray-200 shadow-sm overflow-hidden">
          <div className="p-5">
            <div className="flex items-center">
              <div className="flex-shrink-0 bg-blue-100 rounded-md p-3">
                <DocumentTextIcon className="h-6 w-6 text-blue-700" />
              </div>
              <div className="mr-4">
                <p className="text-sm font-medium text-gray-500">طلبات منجزة</p>
                <p className="text-2xl font-bold text-gray-900">{stats.ordersCompleted}</p>
              </div>
            </div>
            <div className="mt-4 flex items-center">
              <div className="inline-flex items-center">
                <span className="flex h-2.5 w-2.5 rounded-full bg-green-500 mr-1"></span>
                <span className="text-gray-500 text-xs">نسبة إكمال الطلبات: {stats.completionRate}%</span>
              </div>
            </div>
          </div>
          <div className="bg-gray-50 py-3 px-5 border-t border-gray-200">
            <div className="text-sm text-gray-500">
              معدل الاستجابة: <span className="font-semibold text-gray-900">{stats.responseRate}%</span>
            </div>
          </div>
        </div>
        
        <div className="bg-white rounded-xl border border-gray-200 shadow-sm overflow-hidden">
          <div className="p-5">
            <div className="flex items-center">
              <div className="flex-shrink-0 bg-green-100 rounded-md p-3">
                <UserIcon className="h-6 w-6 text-green-700" />
              </div>
              <div className="mr-4">
                <p className="text-sm font-medium text-gray-500">العملاء المتكررون</p>
                <p className="text-2xl font-bold text-gray-900">{stats.returningClients}</p>
              </div>
            </div>
            <div className="mt-4 flex items-center">
              <div className="inline-flex items-center">
                <span className="flex h-2.5 w-2.5 rounded-full bg-indigo-500 mr-1"></span>
                <span className="text-gray-500 text-xs">من إجمالي {Math.round(stats.returningClients / 0.4)} عميل</span>
              </div>
            </div>
          </div>
          <div className="bg-gray-50 py-3 px-5 border-t border-gray-200">
            <div className="text-sm text-gray-500">
              معدل الاحتفاظ: <span className="font-semibold text-gray-900">40%</span>
            </div>
          </div>
        </div>
        
        <div className="bg-white rounded-xl border border-gray-200 shadow-sm overflow-hidden">
          <div className="p-5">
            <div className="flex items-center">
              <div className="flex-shrink-0 bg-yellow-100 rounded-md p-3">
                <svg className="h-6 w-6 text-yellow-700" fill="currentColor" viewBox="0 0 24 24">
                  <path d="M12 17.27L18.18 21l-1.64-7.03L22 9.24l-7.19-.61L12 2 9.19 8.63 2 9.24l5.46 4.73L5.82 21z" />
                </svg>
              </div>
              <div className="mr-4">
                <p className="text-sm font-medium text-gray-500">متوسط التقييم</p>
                <p className="text-2xl font-bold text-gray-900">{stats.avgRating}/5</p>
              </div>
            </div>
            <div className="mt-4 flex items-center">
              <div className="inline-flex">
                {Array.from({ length: 5 }).map((_, i) => (
                  <svg
                    key={i}
                    className={`h-4 w-4 ${i < Math.floor(stats.avgRating) ? 'text-yellow-400' : 'text-gray-300'}`}
                    fill="currentColor"
                    viewBox="0 0 20 20"
                  >
                    <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
                  </svg>
                ))}
              </div>
            </div>
          </div>
          <div className="bg-gray-50 py-3 px-5 border-t border-gray-200">
            <div className="text-sm text-gray-500">
              مجموع التقييمات: <span className="font-semibold text-gray-900">{stats.ordersCompleted}</span>
            </div>
          </div>
        </div>
      </div>
      
      {/* الرسوم البيانية */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <div className="bg-white rounded-xl border border-gray-200 shadow-sm overflow-hidden">
          <div className="px-6 py-5 border-b border-gray-200">
            <h3 className="text-lg font-medium text-gray-900">الأرباح</h3>
          </div>
          <div className="p-6">
            <div className="h-72">
              <Line data={stats.earningsData} options={options} />
            </div>
          </div>
        </div>
        
        <div className="bg-white rounded-xl border border-gray-200 shadow-sm overflow-hidden">
          <div className="px-6 py-5 border-b border-gray-200">
            <h3 className="text-lg font-medium text-gray-900">الطلبات</h3>
          </div>
          <div className="p-6">
            <div className="h-72">
              <Bar data={stats.ordersData} options={options} />
            </div>
          </div>
        </div>
      </div>
      
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <div className="bg-white rounded-xl border border-gray-200 shadow-sm overflow-hidden">
          <div className="px-6 py-5 border-b border-gray-200">
            <h3 className="text-lg font-medium text-gray-900">الطلبات حسب الفئة</h3>
          </div>
          <div className="p-6">
            <div className="h-60">
              <Doughnut data={stats.categoryData} options={doughnutOptions} />
            </div>
          </div>
        </div>
        
        <div className="bg-white rounded-xl border border-gray-200 shadow-sm overflow-hidden">
          <div className="px-6 py-5 border-b border-gray-200">
            <h3 className="text-lg font-medium text-gray-900">توزيع التقييمات</h3>
          </div>
          <div className="p-6">
            <div className="h-60">
              <Doughnut data={stats.ratingDistribution} options={doughnutOptions} />
            </div>
          </div>
        </div>
      </div>
      
      {/* مؤشرات الأداء الرئيسية */}
      <div className="bg-white rounded-xl border border-gray-200 shadow-sm overflow-hidden">
        <div className="px-6 py-5 border-b border-gray-200">
          <h3 className="text-lg font-medium text-gray-900">مؤشرات الأداء الرئيسية</h3>
        </div>
        <div className="p-6">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            <div>
              <h4 className="text-sm font-medium text-gray-500 mb-3">معدل تحويل العملاء</h4>
              <div className="flex items-center justify-between mb-1">
                <span className="text-sm font-medium text-indigo-700">65%</span>
                <span className="text-sm font-medium text-gray-500">الهدف: 70%</span>
              </div>
              <div className="w-full bg-gray-200 rounded-full h-2">
                <div className="bg-indigo-600 h-2 rounded-full" style={{ width: '65%' }}></div>
              </div>
            </div>
            
            <div>
              <h4 className="text-sm font-medium text-gray-500 mb-3">وقت إنجاز الطلبات</h4>
              <div className="flex items-center justify-between mb-1">
                <span className="text-sm font-medium text-indigo-700">3.2 أيام</span>
                <span className="text-sm font-medium text-gray-500">الهدف: 3 أيام</span>
              </div>
              <div className="w-full bg-gray-200 rounded-full h-2">
                <div className="bg-yellow-500 h-2 rounded-full" style={{ width: '93%' }}></div>
              </div>
            </div>
            
            <div>
              <h4 className="text-sm font-medium text-gray-500 mb-3">معدل رضا العملاء</h4>
              <div className="flex items-center justify-between mb-1">
                <span className="text-sm font-medium text-indigo-700">92%</span>
                <span className="text-sm font-medium text-gray-500">الهدف: 90%</span>
              </div>
              <div className="w-full bg-gray-200 rounded-full h-2">
                <div className="bg-green-500 h-2 rounded-full" style={{ width: '100%' }}></div>
              </div>
            </div>
          </div>
        </div>
      </div>
      
      {/* اقتراحات تحسين الأداء */}
      <div className="bg-white rounded-xl border border-gray-200 shadow-sm overflow-hidden">
        <div className="px-6 py-5 border-b border-gray-200">
          <h3 className="text-lg font-medium text-gray-900">اقتراحات لتحسين الأداء</h3>
        </div>
        <div className="divide-y divide-gray-200">
          <div className="p-6">
            <div className="flex">
              <div className="flex-shrink-0">
                <CheckIcon className="h-5 w-5 text-green-500" />
              </div>
              <div className="mr-3">
                <h4 className="text-base font-medium text-gray-900">أضف خدمات جديدة</h4>
                <p className="mt-1 text-sm text-gray-500">
                  تظهر البيانات أن الطلب على خدمات "توليد الصور" و"كتابة المحتوى" مرتفع. يمكنك إضافة مزيد من الخدمات المتخصصة في هذه المجالات.
                </p>
              </div>
            </div>
          </div>
          
          <div className="p-6">
            <div className="flex">
              <div className="flex-shrink-0">
                <CheckIcon className="h-5 w-5 text-green-500" />
              </div>
              <div className="mr-3">
                <h4 className="text-base font-medium text-gray-900">تحسين وقت الاستجابة</h4>
                <p className="mt-1 text-sm text-gray-500">
                  رغم أن معدل الاستجابة ممتاز (98%)، إلا أن تحسين سرعة الرد على الطلبات الجديدة قد يساعد في زيادة معدل تحويل العملاء.
                </p>
              </div>
            </div>
          </div>
          
          <div className="p-6">
            <div className="flex">
              <div className="flex-shrink-0">
                <CheckIcon className="h-5 w-5 text-green-500" />
              </div>
              <div className="mr-3">
                <h4 className="text-base font-medium text-gray-900">تقديم عروض للعملاء المتكررين</h4>
                <p className="mt-1 text-sm text-gray-500">
                  لديك 37 عميل متكرر. يمكنك تقديم خصومات خاصة أو مزايا حصرية لهم لزيادة ولائهم وتشجيعهم على طلب المزيد من الخدمات.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
} 